/**
 * Funkcjonalno≈õƒá galerii produkt√≥w
 */
class ProductGallery {
    constructor() {
        // G≈Ç√≥wny obraz produktu
        this.mainImage = document.querySelector('.main-image img');
        // Miniatury obraz√≥w produktu
        this.thumbnails = document.querySelectorAll('.thumbnail');
        // Aktualny indeks wy≈õwietlanego obrazu
        this.currentIndex = 0;
        // Flaga informujƒÖca, czy trwa przej≈õcie miƒôdzy obrazami
        this.isTransitioning = false;

        // Inicjalizacja galerii, je≈õli istniejƒÖ elementy obrazu g≈Ç√≥wnego i miniatur
        if (this.mainImage && this.thumbnails.length > 0) {
            this.initGallery();
        }
    }

    initGallery() {
        // Dodanie przej≈õƒá CSS do g≈Ç√≥wnego obrazu
        if (this.mainImage) {
            this.mainImage.style.opacity = '1'; // Ustawienie poczƒÖtkowej opopacity na 1
            this.mainImage.style.transition = 'opacity 0.3s ease-in-out'; // Dodanie przej≈õcia opacity
        }

        // Obs≈Çuga klikniƒôƒá miniatur
        this.thumbnails.forEach((thumbnail, index) => {
            thumbnail.addEventListener('click', (e) => {
                e.preventDefault(); // Zapobieganie domy≈õlnej akcji linku
                if (this.isTransitioning) return; // Przerwanie, je≈õli trwa ju≈º przej≈õcie

                const newImage = thumbnail.querySelector('img'); // Pobranie elementu img z miniatury
                // Sprawdzenie, czy nowy obraz istnieje i czy jego ≈∫r√≥d≈Ço jest r√≥≈ºne od aktualnego obrazu g≈Ç√≥wnego
                if (newImage && newImage.src !== this.mainImage.src) {
                    this.switchImage(newImage.src, index); // Prze≈ÇƒÖczenie obrazu
                }
            });
        });

        // Ustawienie poczƒÖtkowego aktywnego stanu dla pierwszej miniatury
        if (this.thumbnails[0]) {
            this.thumbnails[0].classList.add('active'); // Dodanie klasy 'active' do pierwszej miniatury
        }

        // Dodanie nawigacji klawiaturƒÖ
        document.addEventListener('keydown', (e) => {
            if (e.key === 'ArrowLeft') {
                this.previousImage(); // Przej≈õcie do poprzedniego obrazu
            } else if (e.key === 'ArrowRight') {
                this.nextImage(); // Przej≈õcie do nastƒôpnego obrazu
            }
        });

        // Dodanie obs≈Çugi dotyku
        this.setupTouchEvents();
    }

    // Funkcja do prze≈ÇƒÖczania obrazu
    switchImage(newSrc, newIndex) {
        if (this.isTransitioning || newIndex === this.currentIndex) return; // Przerwanie, je≈õli trwa przej≈õcie lub indeks jest taki sam

        this.isTransitioning = true; // Ustawienie flagi przej≈õcia na true

        // Wyciszenie aktualnego obrazu
        this.mainImage.style.opacity = '0'; // Ustawienie opacity na 0

        // Aktualizacja obrazu po wyciszeniu
        setTimeout(() => {
            this.mainImage.src = newSrc; // Zmiana ≈∫r√≥d≈Ça obrazu g≈Ç√≥wnego
            this.mainImage.classList.add('fade-in'); // Dodanie klasy 'fade-in' dla efektu pojawiania siƒô
            this.mainImage.style.opacity = '1'; // Ustawienie opacity na 1, aby obraz by≈Ç widoczny

            // Aktualizacja aktywnej miniatury
            this.thumbnails[this.currentIndex].classList.remove('active'); // Usuniƒôcie klasy 'active' z poprzedniej miniatury
            this.thumbnails[newIndex].classList.add('active'); // Dodanie klasy 'active' do nowej miniatury

            this.currentIndex = newIndex; // Aktualizacja aktualnego indeksu

            // Usuniƒôcie klasy fade-in i reset blokady przej≈õcia po pojawieniu siƒô obrazu
            setTimeout(() => {
                this.mainImage.classList.remove('fade-in'); // Usuniƒôcie klasy 'fade-in'
                this.isTransitioning = false; // Reset flagi przej≈õcia
            }, 300); // Czas trwania animacji przej≈õcia
        }, 300); // Op√≥≈∫nienie przed zmianƒÖ ≈∫r√≥d≈Ça obrazu, odpowiadajƒÖce czasowi wyciszania
    }

    // Funkcja do prze≈ÇƒÖczania na poprzedni obraz
    previousImage() {
        if (this.isTransitioning) return; // Przerwanie, je≈õli trwa przej≈õcie

        // Obliczenie indeksu poprzedniego obrazu
        const newIndex = (this.currentIndex - 1 + this.thumbnails.length) % this.thumbnails.length;
        const newImage = this.thumbnails[newIndex].querySelector('img'); // Pobranie elementu img poprzedniej miniatury

        if (newImage) {
            this.switchImage(newImage.src, newIndex); // Prze≈ÇƒÖczenie obrazu na poprzedni
        }
    }

    // Funkcja do prze≈ÇƒÖczania na nastƒôpny obraz
    nextImage() {
        if (this.isTransitioning) return; // Przerwanie, je≈õli trwa przej≈õcie

        // Obliczenie indeksu nastƒôpnego obrazu
        const newIndex = (this.currentIndex + 1) % this.thumbnails.length;
        const newImage = this.thumbnails[newIndex].querySelector('img'); // Pobranie elementu img nastƒôpnej miniatury

        if (newImage) {
            this.switchImage(newImage.src, newIndex); // Prze≈ÇƒÖczenie obrazu na nastƒôpny
        }
    }

    // Konfiguracja obs≈Çugi zdarze≈Ñ dotykowych
    setupTouchEvents() {
        let touchStartX = 0; // PoczƒÖtkowa pozycja X dotyku
        let touchEndX = 0; // Ko≈Ñcowa pozycja X dotyku

        // Detektor zdarzenia rozpoczƒôcia dotyku na g≈Ç√≥wnym obrazie
        this.mainImage.addEventListener('touchstart', (e) => {
            touchStartX = e.touches[0].clientX; // Zapisanie pozycji X dotkniƒôcia
        }, { passive: true }); // Opcja passive: true dla lepszej responsywno≈õci przewijania

        // Detektor zdarzenia zako≈Ñczenia dotyku na g≈Ç√≥wnym obrazie
        this.mainImage.addEventListener('touchend', (e) => {
            touchEndX = e.changedTouches[0].clientX; // Zapisanie pozycji X po zako≈Ñczeniu dotyku
            const difference = touchStartX - touchEndX; // Obliczenie r√≥≈ºnicy miƒôdzy poczƒÖtkowƒÖ a ko≈ÑcowƒÖ pozycjƒÖ

            if (Math.abs(difference) > 50) { // Sprawdzenie, czy przesuniƒôcie jest wiƒôksze ni≈º 50px (minimalna odleg≈Ço≈õƒá swipe)
                if (difference > 0) {
                    this.nextImage(); // Przej≈õcie do nastƒôpnego obrazu (swipe w lewo)
                } else {
                    this.previousImage(); // Przej≈õcie do poprzedniego obrazu (swipe w prawo)
                }
            }
        }, { passive: true }); // Opcja passive: true dla lepszej responsywno≈õci przewijania
    }
}

/**
 * Funkcjonalno≈õƒá strony produktu
 */
class ProductPage {
    constructor() {
        // Galeria produktu
        this.gallery = null;
        this.initialize(); // Inicjalizacja strony produktu
    }

    initialize() {
        // Inicjalizacja galerii
        this.gallery = new ProductGallery();

        // Inicjalizacja kontrolek ilo≈õci
        this.setupQuantityControls();

        // Inicjalizacja dodawania do koszyka
        this.setupAddToCart();
    }

    // Konfiguracja kontrolek ilo≈õci produktu
    setupQuantityControls() {
        const controls = document.querySelector('.quantity-controls'); // Znalezienie kontrolek ilo≈õci
        if (!controls) return; // Przerwanie, je≈õli kontrolki nie istniejƒÖ

        const input = controls.querySelector('input'); // Znalezienie inputa ilo≈õci
        const minus = controls.querySelector('.minus'); // Znalezienie przycisku minus
        const plus = controls.querySelector('.plus'); // Znalezienie przycisku plus

        // Obs≈Çuga klikniƒôcia przycisku minus
        if (minus && input) {
            minus.addEventListener('click', () => {
                const currentValue = parseInt(input.value); // Pobranie aktualnej warto≈õci z inputa
                if (currentValue > 1) {
                    input.value = currentValue - 1; // Zmniejszenie warto≈õci o 1, je≈õli jest wiƒôksza ni≈º 1
                }
            });
        }

        // Obs≈Çuga klikniƒôcia przycisku plus
        if (plus && input) {
            plus.addEventListener('click', () => {
                const currentValue = parseInt(input.value); // Pobranie aktualnej warto≈õci z inputa
                if (currentValue < 99) {
                    input.value = currentValue + 1; // Zwiƒôkszenie warto≈õci o 1, je≈õli jest mniejsza ni≈º 99
                }
            });
        }

        // Obs≈Çuga zmiany warto≈õci w inpucie ilo≈õci
        if (input) {
            input.addEventListener('change', () => {
                let value = parseInt(input.value); // Parsowanie warto≈õci z inputa
                if (isNaN(value) || value < 1) value = 1; // Ustawienie warto≈õci na 1, je≈õli jest NaN lub mniejsza ni≈º 1
                if (value > 99) value = 99; // Ustawienie warto≈õci na 99, je≈õli jest wiƒôksza ni≈º 99
                input.value = value; // Aktualizacja warto≈õci inputa
            });
        }
    }

    // Konfiguracja dodawania produktu do koszyka
    setupAddToCart() {
        const addToCartBtn = document.querySelector('.add-to-cart'); // Znalezienie przycisku "Dodaj do koszyka"
        if (!addToCartBtn) return; // Przerwanie, je≈õli przycisk nie istnieje

        // Obs≈Çuga klikniƒôcia przycisku "Dodaj do koszyka"
        addToCartBtn.addEventListener('click', () => {
            const productElement = document.querySelector('[data-product-id]'); // Znalezienie elementu produktu po data-product-id
            if (!productElement) return; // Przerwanie, je≈õli element produktu nie istnieje

            // Pobranie danych produktu
            const quantity = parseInt(document.querySelector('.quantity-controls input')?.value || 1); // Pobranie ilo≈õci z inputa lub domy≈õlnie 1
            const productData = {
                id: productElement.dataset.productId, // ID produktu z dataset
                name: document.querySelector('.product-header h1')?.textContent, // Nazwa produktu z nag≈Ç√≥wka
                price: document.querySelector('.price')?.textContent, // Cena produktu
                image: document.querySelector('.main-image img')?.src, // ≈πr√≥d≈Ço g≈Ç√≥wnego obrazu
                quantity: quantity // Ilo≈õƒá produktu
            };

            this.addToCart(productData); // Dodanie produktu do koszyka
        });
    }

    // Funkcja dodajƒÖca produkt do koszyka
    addToCart(productData) {
        // Sprawdzenie, czy obiekt aplikacji i CartManager istniejƒÖ
        if (window.app && window.app.cartManager) {
            if (window.app.cartManager.addToCart(productData)) {
                this.showNotification(); // Wy≈õwietlenie powiadomienia o dodaniu do koszyka
            }
        } else {
            console.error("CartManager not available"); // Wy≈õwietlenie b≈Çƒôdu, je≈õli CartManager nie jest dostƒôpny
        }
    }

    // Wy≈õwietlenie powiadomienia o dodaniu do koszyka
    showNotification() {
        const notification = document.createElement('div'); // Utworzenie elementu div dla powiadomienia
        notification.className = 'added-to-cart-notification glass'; // Dodanie klas CSS
        notification.textContent = 'Dodano do koszyka'; // Ustawienie tekstu powiadomienia

        document.body.appendChild(notification); // Dodanie powiadomienia do body

        // Animacja pojawiania siƒô powiadomienia
        requestAnimationFrame(() => {
            notification.classList.add('show'); // Dodanie klasy 'show', aby rozpoczƒÖƒá animacjƒô
        });

        // Ukrycie i usuniƒôcie powiadomienia po 2 sekundach
        setTimeout(() => {
            notification.classList.remove('show'); // Usuniƒôcie klasy 'show', aby rozpoczƒÖƒá animacjƒô znikania
            setTimeout(() => notification.remove(), 300); // Usuniƒôcie elementu powiadomienia z DOM po zako≈Ñczeniu animacji znikania
        }, 2000); // Czas wy≈õwietlania powiadomienia (2 sekundy)
    }
}

// Inicjalizacja strony produktu po za≈Çadowaniu DOM
document.addEventListener('DOMContentLoaded', () => {
    // Sprawdzenie, czy na stronie znajduje siƒô element .product-details (co sugeruje stronƒô produktu)
    if (document.querySelector('.product-details')) {
        new ProductPage(); // Utworzenie nowej instancji ProductPage, inicjalizujƒÖc funkcjonalno≈õƒá strony produktu
    }
});
